const ValidasiFormDataMemberPenjualan = (value) => {
    const errors = {};
    if (!value.kode_sales) {
        errors.kode_sales = "Kode Sales Harus Dipilih";
    }
    if (!value.nama_customer) {
        errors.nama_customer = "Nama Customer Harus Diisi";
    }
    if (!value.no_hp) {
        errors.no_hp = "No Hp Harus Diisi";
    }
    if (!value.alamat) {
        errors.alamat = "Alamat Harus Diisi";
    }

    return errors;
}

export default ValidasiFormDataMemberPenjualan;


