import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getDataSales } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import { getData } from "../../../config/axios";
import ValidasiFormDataMemberPenjualan from "../../../Validasi/Penjualan/ValidasiFormDataMemberPenjualan";

const maptostate = (state) => {
  let data_customer =
    localStorage.getItem("data_customer_pembelian_tmp") === null || undefined
      ? []
      : JSON.parse(localStorage.getItem("data_customer_pembelian_tmp") || []);
  return {
    initialValues: {
      kode_sales: data_customer.kode_sales || "",
      jenis_pelanggan: data_customer.jenis_pelanggan || "",
      kode_customer: data_customer.kode_customer || "",
      nama_customer: data_customer.nama_customer || "",
      no_hp: data_customer.no_hp || "",
      alamat_customer: data_customer.alamat_customer || "",
    },
    DataSales: state.datamaster.getDataSales,
  };
};

const jenis_pelanggan = [
  {
    value: "MEMBER",
    name: "MEMBER",
  },
  {
    value: "NONMEMBER",
    name: "NON MEMBER",
  },
];
class ModalDataCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showMember: true,
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    let data_customer =
      localStorage.getItem("data_customer_pembelian_tmp") === null || undefined
        ? []
        : JSON.parse(localStorage.getItem("data_customer_pembelian_tmp") || []);
    if (data_customer && data_customer.jenis_pelanggan === "MEMBER") {
      this.setState({
        showMember: false,
      });
    } else {
      this.setState({
        showMember: true,
      });
    }
  }
  changemember(e) {
    // console.log(e);
    if (e === "MEMBER") {
      this.setState({
        showMember: false,
      });
    } else {
      this.props.change("kode_customer", "");
      this.props.change("nama_customer", "");
      this.props.change("no_hp", "");
      this.props.change("alamat_customer", "");
      this.setState({
        showMember: true,
      });
    }
  }

  getMember(e) {
    // console.log(e);
    getData("member/get/by-kode-member/" + e)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Member Yang Anda Masukan Tidak Ada !!!");
        } else {
          res.data.forEach((item, index) => {
            this.props.change("nama_customer", item.nama_customer);
            this.props.change("no_hp", item.no_hp);
            this.props.change("alamat_customer", item.alamat_customer);
          });
        }
      })
      .catch((err) => {
        ToastNotification(
          "info",
          "Terjadi Kesalahan SIlahkan Ulangi Kembali !!!"
        );
      });
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              tabIndex="1"
              name="kode_sales"
              label="Pilih Kode Sales"
              placeholder="Masukan Kode Sales"
              options={this.props.DataSales.filter(
                (list) => list.kode_sales !== undefined
              ).map((list) => {
                let data = {
                  value: list.kode_sales,
                  name: list.kode_sales,
                };
                return data;
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              tabIndex="2"
              name="jenis_pelanggan"
              label="Pilih Pelanggan"
              onChange={(e) => this.changemember(e)}
              placeholder="Silahkan Pilih Pelanggan"
              options={jenis_pelanggan}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              tabIndex="3"
              name="kode_customer"
              onChange={(e) => this.getMember(e.target.value)}
              component={ReanderField}
              type="text"
              readOnly={this.state.showMember}
              label="Kode Customer"
              placeholder="Masukan Kode Customer"
            />
          </div>
          <div className="col-4">
            <Field
              tabIndex="4"
              name="nama_customer"
              component={ReanderField}
              type="text"
              label="Nama Customer"
              placeholder="Masukan Nama Customer"
            />
          </div>
          <div className="col-4">
            <Field
              tabIndex="5"
              name="no_hp"
              component={ReanderField}
              type="number"
              label="No Hp"
              placeholder="Masukan No Hp"
            />
          </div>
          <div className="col-4">
            <Field
              tabIndex="6"
              name="alamat_customer"
              component={ReanderField}
              type="textarea"
              label="Alamat Customer"
              placeholder="Masukan Alamat Customer"
            />
          </div>
        </div>
        <br />
        <br />
        <br />
        <div className="row">
          <div className="col-6 text-left">
            <button
              onClick={() => this.props.back()}
              className="btn btn-secondary"
              type="button"
            >
              Kembali
            </button>
          </div>
          <div className="col-6 text-right">
            <button className="btn btn-primary" type="submit"
            disabled={this.props.isLoading}
            >
            {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Selesai Pembelian
                </>
              ) : (
                "Selesai Pembelian"
              )}
            </button>
          </div>
        </div>
        {/* <div className="row">
          <div className="col-8"></div>
          <div className="col-4">
            <div className="col-12 text-right">
              <button className="btn btn-secondary"
              onClick={()=>this.props.back()}
              > Back </button>
              &nbsp;
              <button
              tabIndex="7"
                className="btn btn-primary"
                type="submit"
              >
                {" "}
                Next{" "}
              </button>
            </div>
          </div>
        </div> */}
      </form>
    );
  }
}

ModalDataCustomer = reduxForm({
  form: "ModalDataCustomer",
  enableReinitialize: true,
  validate: ValidasiFormDataMemberPenjualan,
})(ModalDataCustomer);
export default connect(maptostate, null)(ModalDataCustomer);
