import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import {
  getDataJenis,
  getDataSales,
  getKondisiPembelian,
  // showModal,
} from "../../../actions/datamaster_action";
import {
  HiidenFiled,
  ReanderField,
  ReanderSelect,
} from "../../../components/helpers/field";
import { getItem } from "../../../components/helpers/function";
import { ToastNotification } from "../../../components/helpers/notification";
import { getData } from "../../../config/axios";
import ValidasiPembelian from "../../../Validasi/Pembelian/ValidasiPembelian";
import Loading from "react-fullscreen-loading";
import { Gramasi } from "../../../Validasi/normalize";
// import ModalGlobal from "../../../components/helpers/ModalGlobal";
// import OtorisasiPembelian from "./OtorisasiPembelian";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});

class ModalTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      harga_jual: true,
      hargapergram: true,
      ongkos: true,
      total: true,
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getKondisiPembelian());
    setTimeout(() => {
      document.getElementById("kode_barcode").focus();
    }, 100);
  }
  getBarcode(e) {
    clearTimeout(this.delay);
    this.delay = setTimeout(() => {
      this.setState({
        searching: true,
      });
      let cek =
        JSON.parse(localStorage.getItem("data_barang_pembelian_tmp")) || [];
      document.getElementById("kode_barcode").focus();
      let hasil_cek = cek.findIndex((hasil) => hasil.kode_barcode === e);
      if (hasil_cek === -1) {
        getData("pembelian/get/jual/" + e)
          .then((res) => {
            if (res.data.length === 0) {
              ToastNotification("info", "Data Yang Dicari Tidak Ada");
              this.props.change("nama_barang", "");
              this.props.change("nama_atribut", "");
              this.props.change("berat_atribut", "");
              this.props.change("harga_atribut", "");
              this.props.change("berat", "");
              this.props.change("berat_nota", "");
              this.props.change("kode_dept", "");
              this.props.change("harga", "");
              this.props.change("harga_nota", "");
              this.setState({
                searching: false,
              });
            } else {
              this.setState({
                searching: false,
              });
              res.data.forEach((list, index) => {
                let datamember = getItem("data_customer_pembelian_tmp");
                // console.log(datamember.kode_customer);
                if (datamember === null || datamember.length === 0) {
                  let data_cusomer = {
                    kode_sales: list.kode_sales,
                    jenis_pelanggan:
                      list.kode_member === "NONMEMBER" ? "NONMEMBER" : "MEMBER",
                    kode_customer: list.kode_member,
                    nama_customer: list.nama_customer,
                    alamat_customer: list.alamat_customer,
                    no_hp: list.no_hp,
                  };
                  // document.getElementById("berat").focus();
                  localStorage.setItem(
                    "data_customer_pembelian_tmp",
                    JSON.stringify(data_cusomer)
                  );
                  this.props.change("nama_barang", list.nama_barang);
                  this.props.change("nama_atribut", list.nama_atribut);
                  this.props.change("berat_atribut", list.berat_atribut);
                  this.props.change("harga_atribut", list.harga_atribut);
                  this.props.change("berat_nota", list.berat);
                  this.props.change("berat", list.berat);
                  this.props.change("kode_dept", list.kode_dept);
                  this.props.change("harga_nota", parseInt(list.harga_jual) + parseInt(list.harga_atribut));
                  this.setState({
                    harga_jual: false,
                    hargapergram: false,
                    ongkos: false,
                  });
                } else {
                  this.setState({
                    searching: false,
                  });
                  if (list.kode_member !== datamember.kode_customer) {
                    ToastNotification("info", "Data Customer Berbeda");
                    return false;
                  } else {
                    let data_cusomer = {
                      kode_sales: list.kode_sales,
                      jenis_pelanggan:
                        list.kode_member === "NONMEMBER"
                          ? "NONMEMBER"
                          : "MEMBER",
                      kode_customer: list.kode_member,
                      nama_customer: list.nama_customer,
                      alamat_customer: list.alamat_customer,
                      no_hp: list.no_hp,
                    };
                    // document.getElementById("berat").focus();
                    localStorage.setItem(
                      "data_customer_pembelian_tmp",
                      JSON.stringify(data_cusomer)
                    );
                    this.props.change("nama_barang", list.nama_barang);
                    this.props.change("nama_atribut", list.nama_atribut);
                    this.props.change("berat_atribut", list.berat_atribut);
                    this.props.change("harga_atribut", list.harga_atribut);
                    this.props.change("berat_nota", list.berat);
                    this.props.change("berat", list.berat);
                    this.props.change("kode_dept", list.kode_dept);
                    this.props.change("harga_nota", parseInt(list.harga_jual) + parseInt(list.harga_atribut));
                    this.setState({
                      harga_jual: false,
                      hargapergram: false,
                      ongkos: false,
                      searching: false,
                    });
                  }
                }
              });
            }
          })
          .catch((err) => {
            console.log(err);
            this.props.change("nama_barang", "");
            this.props.change("nama_atribut", "");
            this.props.change("berat_atribut", "");
            this.props.change("harga_atribut", "");
            this.props.change("berat", "");
            this.props.change("kode_dept", "");
            this.props.change("harga", "");
            ToastNotification("info", "Data Yang Dicari Tidak Ada");
            this.setState({
              searching: false,
            });
          });
      } else {
        this.props.change("nama_barang", "");
        this.props.change("nama_atribut", "");
        this.props.change("berat_atribut", "");
        this.props.change("harga_atribut", "");
        this.props.change("berat", "");
        this.props.change("kode_dept", "");
        this.props.change("harga", "");
        ToastNotification("info", "Kode Barcode Sudah Ada Dalam Table");
        this.setState({
          searching: false,
        });
      }
    }, 1500);
  }
  hitungTotal() {
    this.props.change("harga_rata", this.props.harga_rata);
    // console.log(this.props.harga_beli);
  }

  setValue(e) {
    // console.log(e);
    // console.log(e === "" ? 'bener' : 'salah');
    // this.props.change("harga_rata", );

    if (e === "") {
      this.change("berat_nota", 1);
      // document.getElementById("berat_nota")[0].value = 0;
    }
  }

  getHarga(e) {
    // console.log(e);
    getData("parabeli/get/by-kode-kondisi/" + e).then((res) => {
      this.props.change("potongan", res.data.potongan);
      // this.hitungTotal()
      this.props.change("harga", this.props.harga_beli);
    });
  }

  // otorisasi(e) {
  //   let harus_bayar2 = e.replace("Rp.", "");
  //   let harus_bayar3 = harus_bayar2.replaceAll(",", "");
  //   if (parseFloat(harus_bayar3) > parseFloat(this.props.harga_nota)) {
  //     this.props.dispatch(showModal());
  //   }
  // }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-12 col-md-4">
            <Field
              id="potongan"
              name="potongan"
              component={HiidenFiled}
              type="hidden"
              readOnly={true}
            />
            <Field
              onChange={(e) => this.getBarcode(e.target.value)}
              id="kode_barcode"
              name="kode_barcode"
              label="Kode Barcode"
              type="text"
              normalize={(value) => value.replace(/[^\d]/g, "")}
              placeholder="Masukan Kode Barcode"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              name="kondisi"
              label="Kondisi"
              onChange={(e) => this.getHarga(e)}
              options={this.props.datakondisi
                .filter((list) => list.kondisi_barang !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kondisi_barang,
                    name: list.kondisi_barang,
                  };
                  return data || [];
                })}
              placeholder="Masukan Kondisi"
              component={ReanderSelect}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              label="Kode Jenis"
              name="kode_dept"
              options={this.props.datajenis
                .filter((list) => list.kode_dept !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_dept,
                    name: list.kode_dept + " - " + list.nama_dept,
                  };
                  return data || [];
                })}
              placeholder={"Silahkan Pilih Kode Jenis"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              name="nama_barang"
              label="Nama Barang"
              type="text"
              placeholder="Masukan Nama Barang"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              name="berat_nota"
              label="Berat Nota"
              readOnly={true}
              normalize={Gramasi}
              placeholder="Masukan Berat Nota"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              id="berat"
              name="berat"
              label="Berat"
              // type="number"
              normalize={Gramasi}
              onChange={this.hitungTotal()}
              placeholder="Masukan Berat"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              name="harga_nota"
              label="Harga Nota"
              type="telp"
              readOnly={true}
              onChange={this.hitungTotal()}
              {...currencyMask}
              placeholder="Masukan Harga Nota"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              name="harga"
              label="Harga Beli"
              type="telp"
              {...currencyMask}
              // onBlur={(e) => this.otorisasi(e.target.value)}
              onChange={this.hitungTotal()}
              placeholder="Masukan Harga Beli"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-4">
            <Field
              name="harga_rata"
              label="Harga Rata"
              type="telp"
              readOnly={true}
              {...currencyMask}
              placeholder="Masukan Harga Rata"
              component={ReanderField}
            />
          </div>

          <div className="col-12 col-md-12 text-right">
            <label>&nbsp;</label>
            <button
              className="btn btn-primary"
              disabled={this.props.isLoadingSimpanBarang}
              type="submit"
            >
              {this.props.isLoadingSimpanBarang ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Barang"
              )}
            </button>
          </div>
            {/* <ModalGlobal  title="Otorisasi Pembelian"  content={<OtorisasiPembelian />} /> */}
        </div>
        {this.state.searching ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

// ModalTambahBarang = reduxForm({
//   form: "ModalTambahBarang",
//   enableReinitialize: true,
//   validate: ValidasiMasterDataHadiah,
// })(ModalTambahBarang);
// export default connect(maptostate, null)(ModalTambahBarang);

ModalTambahBarang = reduxForm({
  form: "ModalTambahBarang",
  enableReinitialize: true,
  validate: ValidasiPembelian,
})(ModalTambahBarang);

const selector = formValueSelector("ModalTambahBarang"); // <-- same as form name
export default connect((state) => {
  const { harga, berat, potongan, harga_nota } = selector(
    state,
    "harga",
    "berat",
    "harga_nota",
    "potongan"
  );
  let potongan_tmp = parseInt(potongan || 0) * parseFloat(berat || 0);
  // console.log(harga_nota);
  // console.log(potongan_tmp);
  return {
    setBarangPembelian: state.penjualan.setBarangPembelian,
    harga_rata: parseFloat(harga || 0) / parseFloat(berat || 0) || 0,
    harga_potong: parseInt(harga_nota || 0) - parseInt(potongan_tmp || 0),
    harga_beli: parseInt(harga_nota || 0) - parseInt(potongan_tmp || 0),
    DataSales: state.datamaster.getDataSales,
    datajenis: state.datamaster.getDataJenis,
    harga_nota: harga_nota,
    datakondisi: state.datamaster.getKondisiPembelian,
  };
})(ModalTambahBarang);
