import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { getDataSales } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { NumberOnly } from "../../../Validasi/normalize";
import ValidasiPembayaran from "../../../Validasi/Penjualan/ValidasiPembayaran";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
const jenis_pembayaran = [
  {
    value: "CASH",
    name: "CASH",
  },
];
class ModalPembayaran extends Component {
  state = {};
  componentDidMount() {
    this.props.dispatch(getDataSales());
    let data_barang_penjualan = JSON.parse(
      localStorage.getItem("data_barang_pembelian_tmp")
    );
    let total = 0;
    data_barang_penjualan.forEach((item, index) => {
      // console.log(item.total);
      total = total + item.harga;
      this.props.change("grand_total_pembayaran", total);
    });
    this.props.change("jenis", "CASH");
    setTimeout(() => {
      document.getElementById("cash_trx_pembelian").focus();
    }, 100);
  }
  hitungTotal() {
    this.props.change("kembali", this.props.kembali);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <br />
        <br />
        <div className="row">
          <div className="col-6">
            <Field
              name="grand_total_pembayaran"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              readOnly={true}
              {...currencyMask}
              label="Grand Total Pembayaran"
              placeholder="Masukan Grand Total"
            />
          </div>
          <div className="col-6">
            <Field
              name="jenis"
              label="Pilih Jenis Bayar"
              placeholder="Pilih Jenis Pembayaran"
              options={jenis_pembayaran}
              component={ReanderSelect}
            />
          </div>
          <div className="col-6">
            <Field
              tabIndex="1"
              name="cash"
              id="cash_trx_pembelian"
              component={ReanderField}
              type="text"
              label="Cash"
              onChange={this.hitungTotal()}
              {...currencyMask}
              normalize={NumberOnly}
              placeholder="Masukan Total Pembayaran"
            />
          </div>
          <div className="col-6">
            <Field
              name="kembali"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Kembali"
              {...currencyMask}
              normalize={NumberOnly}
              placeholder="Kembalian"
            />
          </div>
        </div>

        <br />
        <br />
        <br />
        <div className="row">
          <div className="col-6 text-left">
            <button
              onClick={() => this.props.back()}
              className="btn btn-secondary"
              type="button"
            >
              Kembali
            </button>
          </div>
          <div className="col-6 text-right">
          <button
              tabIndex="2"
              className="btn btn-primary "
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Diproses
                </>
              ) : (
                "Bayar Sekarang"
              )}
            </button>
          </div>
        </div>
        {/* <div className="row">
          <div className="col-6"></div>
          <div className="col-6 text-right">
            <button
              className="btn btn-secondary"
              onClick={() => this.props.back()}
            >
              {" "}
              Back{" "}
            </button>
            &nbsp;
            <button
              tabIndex="2"
              className="btn btn-primary "
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Diproses
                </>
              ) : (
                "Bayar Sekarang"
              )}
            </button>
           
          </div>
        </div> */}
      </form>
    );
  }
}

ModalPembayaran = reduxForm({
  form: "ModalPembayaran",
  enableReinitialize: true,
  validate: ValidasiPembayaran,
})(ModalPembayaran);

const selector = formValueSelector("ModalPembayaran"); // <-- same as form name

export default connect((state) => {
  const { grand_total_pembayaran, cash } = selector(
    state,
    "grand_total_pembayaran",
    "cash"
  );
  return {
    kembali: parseFloat(grand_total_pembayaran || 0) - parseFloat(cash || 0),
  };
})(ModalPembayaran);
